.parent {
  position: fixed;
  top: 0;
  width: 1000px;
}

.navbar {
  display: flex;
  justify-content: space-around;
  background-color: black;
}

.navbar a {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;

  text-decoration: none;
  background-color: inherit;
  color: white;
  padding: 20px;
  /* font-family: 'Open Sans'; */
}

.navbar a:hover {
  color: #3399ff;
}
