.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 50px;
}

.row h1 {
  font-family: 'Fjalla One', sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 43px;
  margin-bottom: -6px;
}

.project-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  align-content: space-between;
}

.project-card {
  height: 300px;
  width: 300px;
  background-color: green;
}
