.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 50px;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.row h1 {
  font-family: 'Fjalla One', sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 43px;
  margin-bottom: -6px;
}

.row h4 {
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1px;
  margin-bottom: 0px;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.column p {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  line-height: 23px;
  margin: 0 0 23px 0;
  text-align: center;
  /* font-size: 20px;  */
}

.column h5 {
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1px;
  margin-bottom: -6px;
  margin-top: 0px;
}

.resume-button {
  background-color: black;
  border: none;
  height: 40px;
  margin: auto;
}

.resume-button a {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  text-decoration: none;
  color: white;
  /* font-size: 20px; */
}

.resume-button a:hover {
  color: #3399ff;
}

.column ul {
  list-style-type:circle;
  padding-left: 5px;
  margin-left: 15px;
}

.column li {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  line-height: 23px;
  margin: 0 0 10px 0;
}
